import React, { forwardRef } from 'react'
import Field from '../Field'
import eyesSrc from 'src/assets/icon/eyes.svg'
import useStyles from './useStyles'

interface Props {
  label: string
  [key: string]: unknown
}

const PasswordField: React.FC<Props> = forwardRef(({ lebel, ...restProps }, ref) => {
  const classes = useStyles()
  const [isShowPassword, setIsShowPassword] = React.useState(false)

  const handleClickShowPassword = () => {
    setIsShowPassword((isShow) => !isShow)
  }

  return (
    <Field
      {...restProps}
      ref={ref}
      lebel={lebel}
      type={isShowPassword ? 'text' : 'password'}
      endAdornment={<img className={classes.eyes} onClick={handleClickShowPassword} src={eyesSrc} alt='' />}
    />
  )
})

PasswordField.displayName = 'PasswordField'

export default PasswordField
