import { MenuItem } from '@material-ui/core'
import { Select } from '@material-ui/core'
import React from 'react'
import { useTranslation } from 'react-i18next'
import getNavigatorLanguage from 'src/utils/getNavigatorLanguage'
import { ReactComponent as ArrowDown } from 'src/assets/icon/arrow-down-icon.svg'
import { useDispatch } from 'react-redux'
import { setLanguage as setLanguageToStore } from 'src/store/languageSlice'
// import useStyles from './useStyles'

interface Props {
  [key: string]: unknown
}

const getCurrentLanguage = () => {
  const currentLanguage = window.localStorage.getItem('language')
  return !currentLanguage ? getNavigatorLanguage() : currentLanguage
}

const LanguageButton: React.FC<Props> = ({ ...restProps }) => {
  const [language, setLanguage] = React.useState(getCurrentLanguage)
  const { i18n } = useTranslation()
  const dispatch = useDispatch()
  const [anchor, setAnchor] = React.useState<Element>()
  React.useEffect(() => {
    //解popper位置異常的問題, 需在languageButton 的元件加一個classname langBtn
    //ex:  <LanguageButton className={`${classes.languageButton} langBtn`} />
    const anchorRef = document.getElementsByClassName('langBtn')[0]
    setAnchor(anchorRef)
  }, [])

  const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    const newLanguage = event.target.value as string

    window.localStorage.setItem('language', newLanguage)
    dispatch(setLanguageToStore(newLanguage))
    setLanguage(newLanguage)
    i18n.changeLanguage(newLanguage)
  }

  return (
    <Select
      value={language}
      onChange={handleChange}
      IconComponent={ArrowDown}
      MenuProps={{
        anchorOrigin: {
          vertical: 'bottom',
          horizontal: 'left',
        },
        MenuListProps: { style: { width: '100px' } },
        getContentAnchorEl: null,
        anchorEl: anchor,
      }}
      {...restProps}
    >
      <MenuItem value='zh-TW'>繁體中文</MenuItem>
      <MenuItem value='en'>English</MenuItem>
    </Select>
  )
}

export default LanguageButton
