import React from 'react'
import classNames from 'classnames'
import PasswordField from 'src/shared/components/PasswordField'
import useFormConfig from './formConfig'
import Button from '../../../Button'
import { FormHelperText, Typography } from '@material-ui/core'
import { sendEventType } from '../..'
import useResetPasswordToken from 'src/reset/service/getResetPasswordToken/hooks'
import Loading from 'src/shared/components/Loading'
import useResetPassword from 'src/reset/service/patchResetPassword/hooks'
import useStyles from './useStyles'

interface Props {
  send: sendEventType
}

const ResetForm: React.FC<Props> = ({ send }) => {
  const classes = useStyles()
  const { mutate } = useResetPassword(send)
  const { isFetched, token } = useResetPasswordToken(send)
  const { register, handleSubmit, onSubmit, errors } = useFormConfig(mutate, token)

  const helperText = classNames({
    [classes.heplerText]: true,
    [classes.heplerTextError]: errors.password?.message,
  })

  return isFetched ? (
    <>
      <Typography variant='h5' className={classes.mainTitle}>
        重置密碼
      </Typography>
      <form className={classes.form} onSubmit={handleSubmit(onSubmit)}>
        <PasswordField ref={register} label='新密碼' name='password' error={Boolean(errors.password)} />
        <FormHelperText className={helperText}>需擁有1個英文字母、1個數字、8個字元</FormHelperText>
        <PasswordField
          ref={register}
          label='確認新密碼'
          name='confirmPassword'
          error={Boolean(errors.confirmPassword)}
          errorMessage={errors?.confirmPassword?.message}
        />
        <Button type='submit'>確定</Button>
      </form>
    </>
  ) : (
    <Loading />
  )
}

export default ResetForm
